import{raf,cancelRaf}from'@vant/use';import{getScrollTop,setScrollTop}from'../utils';export function scrollLeftTo(scroller,to,duration){let rafId;let count=0;const from=scroller.scrollLeft;const frames=duration===0?1:Math.round(duration*1000/16);function cancel(){cancelRaf(rafId);}function animate(){scroller.scrollLeft+=(to-from)/frames;if(++count<frames){rafId=raf(animate);}}animate();return cancel;}export function scrollTopTo(scroller,to,duration,callback){let rafId;let current=getScrollTop(scroller);const isDown=current<to;const frames=duration===0?1:Math.round(duration*1000/16);const step=(to-current)/frames;function cancel(){cancelRaf(rafId);}function animate(){current+=step;if(isDown&&current>to||!isDown&&current<to){current=to;}setScrollTop(scroller,current);if(isDown&&current<to||!isDown&&current>to){rafId=raf(animate);}else if(callback){rafId=raf(callback);}}animate();return cancel;}